<template>
  <Col>
    <div v-for="(item,index) in textsList" :key="item.textId" style="margin: 5px;padding: 5px">
      <Button long @click="goText(item)">
        <Row style="margin: 10px">
          <Col
            style="color:white;font-size: large;font-weight: bold;background-color: deeppink;border-radius: 5px;text-align: center"
            span="2">
            {{index+1}}
          </Col>
          <Col offset="3">
           <span style="font-size: large;font-weight: bold;color: #000;">
             {{item.textTitle}} ---
           </span>
            <span style="font-size:18px;color: #000;">
                       <Icon type="md-person" color="gray"/>
            {{item.user.userName}}
           </span>
            <span style="float:right;font-size:18px;color: #000;">
           <Icon type="ios-heart" style="margin: 5px" color="gray" size="small" />{{item.textLikenum}}

           </span>
          </Col>
        </Row>
      </Button>
    </div>
  </Col>
</template>

<script>

  export default {
    name: "TextByLike",
    data() {
      return {
        textsList: []
      }
    }, methods: {
      getTextTop() {
        this.axios.get('textByTopLike').then(res => {
          if (res.data.code === 100) {
            this.textsList = res.data.map.texts
          }
        }).catch(error => {
          this.$Message.warning("获取点赞榜是啊比")
        })
      }, goText(item) {
        console.log(item)
        this.$router.push('text?textId='+item.textId)
      }
    }, components: {}, mounted() {
      this.getTextTop()
    }
  }
</script>

<style scoped>

</style>



